import { initCamera, initDocument, webFrameModel } from "./unico-webframe.js";
import { Camera } from "../../../data/camera/api/Camera.js";
import { UnicoSDKTheme } from "./modules/UnicoSDKTheme.js";
import { DocumentCameraType } from "../../../entities/CameraType.js";

export class UnicoCamera extends Camera {

    constructor() {
        super();
    
        const KEY_CAMERA_TYPE = "cameraType";

        super.initCamera = function(config, theme) {
            return new Promise((resolve, reject) => {
                const callback = {
                    on: {
                        success: function(result) {
                            resolve(result);
                        },
                        error: function(e) {
                            reject(e);
                        },
                        support: function(e) {
                            console.log(e);
                        }
                    }
                }

                let _theme = UnicoSDKTheme.getTheme(theme);

                try {
                    if(isDocumentCamera(config)) {
                        initDocument({ ...config, TYPE: config[KEY_CAMERA_TYPE].code, optional: {LABEL_DOCUMENT_TYPE_OTHERS: config[KEY_CAMERA_TYPE].description }  }, callback, _theme);
                    } else {
                        initCamera({ ...config, TYPE: config[KEY_CAMERA_TYPE].code }, callback, _theme);
                    }
                } catch(e) {
                    reject(e);
                }
            });
        }

        super.getUserAgent = function() {
            return "unico-webframe-js";
        }

        super.loadModels = function(config, theme) {
            return webFrameModel.loadModelsCameraInteligence(config);
        }

        let isDocumentCamera = function(config) {
            return typeof config === "object" &&
                config.hasOwnProperty(KEY_CAMERA_TYPE) && 
                config[KEY_CAMERA_TYPE] instanceof DocumentCameraType;
                
        }
    }
}