import { AuthenticatorData } from "../api/AuthenticatorData.js"
import { UnicoException } from "../../../entities/UnicoException.js";
import { UnicoHttpRequest } from "../../../plugins/networking/UnicoHttpRequest.js";

export class AuthenticatorRepository extends AuthenticatorData {

    /**
     * Authenticator Repository
     * 
     * @param {UnicoHttpRequest} httpRequest 
     */
    constructor(httpRequest) {
        super();

        if(httpRequest == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("httpRequest");
        }

        if(!(httpRequest instanceof UnicoHttpRequest)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("httpRequest", "UnicoHttpRequest");
        }

        super.loadJSON = function(jsonPath) {

            if(jsonPath == null || jsonPath == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("jsonPath");
            }
            
            if(typeof jsonPath !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("jsonPath", "String");
            }

            return httpRequest.get(jsonPath);
        }

        super.authenticate = function(host, apiKey, origin) {

            if(host == null || host == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("host");
            }

            if(typeof host !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("host", "String");
            }

            if(apiKey == null || apiKey == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("apiKey");
            }

            if(typeof apiKey !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("apiKey", "String");
            }

            if(origin == null || origin == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("origin");
            }

            if(typeof origin !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("origin", "String");
            }
            
            return httpRequest.changeBaseURL(host).post(
                "/services/v3/acessoservice.svc/token/sdk", 
                { 
                    "APIKEY" : apiKey,
                },
                { 
                    "packageName": origin
                }
            );
        }
    }
}