import { LocaleType } from "../../entities/LocaleType.js"
import { UnicoException } from "../../entities/UnicoException.js";

import { pt_BR } from "../../../../resources/strings/pt_BR.js"

export class Locale {
    
    constructor(locale) {
        
        const languages = {
            pt_BR
        }

        if(locale == null) {
            throw UnicoException.GENERAL_MISSING_PROPERTY("locale");
        }

        if(!(locale instanceof LocaleType)) {
            throw UnicoException.GENERAL_INSTANCE_OF("locale", "LocaleType");
        }

        const dictonary = languages[locale.name];

        if(dictonary === undefined){
            throw UnicoException.LOCALE_NOT_FOUND(locale);
        }

        this.getString = function(text) {

            if(text == null || text == "") {
                throw UnicoException.GENERAL_MISSING_PROPERTY("text");
            }

            if(typeof text !== "string") {
                throw UnicoException.GENERAL_INSTANCE_OF("text", "String");
            }

            if(!dictonary.hasOwnProperty(text)) {
                throw UnicoException.LOCALE_MISSING_TEXT(text);
            }
            
            return dictonary[text];

        }

    }

}