import { EncryptUseCase } from "../../../../../domain/encryptor/useCases/EncryptUseCase.js";
import { GetSessionUseCase } from "../../../../../domain/liveness/useCases/GetSessionUseCase.js";
import { UnicoException } from "../../../../../entities/UnicoException.js";
import { SelfieCameraPresentation } from "../api/SelfieCameraPresentation.js";
import { GetSystemInfoUseCase } from "../../../../../domain/system/useCases/GetSystemInfoUseCase.js"

export class InteractiveSelfieCameraPresenter extends SelfieCameraPresentation {

    /**
     * Implementation of SelfieCamera Presentation
     */
    constructor(getSessionUseCase, encryptUseCase, getSystemInfoUseCase) {
        super();

        const TOKEN_KEY = "Token";
        const KEY_FACE_SCAN = "faceScan"
        const KEY_LOW_QUALITY_AUDIT_TRAIL = "lowQualityAuditTrail"
        const KEY_AUDIT_TRAIL = "auditTrail"

        if(getSessionUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("getSessionUseCase");
        }

        if(!(getSessionUseCase instanceof GetSessionUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("getSessionUseCase", "GetSessionUseCase");
        }

        if(encryptUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("encryptUseCase");
        }

        if(!(encryptUseCase instanceof EncryptUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("encryptUseCase", "EncryptUseCase");
        }

        if(getSystemInfoUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("getSystemInfoUseCase");
        }

        if(!(getSystemInfoUseCase instanceof GetSystemInfoUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("getSystemInfoUseCase", "GetSystemInfoUseCase");
        }

        super.getCameraSession = function(host, userAgent, apiKey, token) {
            return new Promise(async (resolve, reject) => {
                var session;
                
                try {
                    session = await getSessionUseCase.execute(host, userAgent, apiKey, token);
                } catch(e) {
                    reject(UnicoException.SESSION_REQUEST_ERROR);
                    return;
                }
                
                if(!validateSession(session)) {
                    reject(UnicoException.SESSION_TOKEN_NOT_FOUND);
                    return;
                }
                
                resolve(session[TOKEN_KEY]);
            });
        }

        super.getSystemInfo = async function() {
            try {
                return getSystemInfoUseCase.execute();
            } catch(e) {
                throw UnicoException.INFO_ERROR;
            }
        }

        super.encrypt = async function(result, signature) {
            try {
                return encryptUseCase.execute(result, signature)
            } catch(e) {
                throw UnicoException.ENCRYPTION_ERROR;
            }
        }

        super.validate = async function(result) {
            if(validateString(result, KEY_FACE_SCAN) &&
                validateArray(result, KEY_LOW_QUALITY_AUDIT_TRAIL) &&
                validateArray(result, KEY_AUDIT_TRAIL)
            ) {
                return result;
            } else {
                throw UnicoException.CAMERA_PARSE_ERROR;
            };
        }

        let validateSession = function(session) {
            return typeof session == "object" &&
                session.hasOwnProperty(TOKEN_KEY) &&
                session[TOKEN_KEY] != null &&
                session[TOKEN_KEY] != ""
        }

        let validateString = function(obj, key) {
            return obj !== undefined &&
                obj != null &&
                typeof obj === "object" &&
                obj.hasOwnProperty(key) && 
                typeof obj[key] === "string" &&
                obj[key] != null &&
                obj[key] != ""
        }

        let validateArray = function(obj, key) {
            return obj !== undefined &&
                obj != null &&
                typeof obj === "object" &&
                obj.hasOwnProperty(key) && 
                typeof obj[key] === "object" &&
                obj[key] != null &&
                obj[key].length > 0 
        }
    }
}