import { loadInjectors } from "../../../di/DependencyInjector.js";

/**
 * Inject and return
 */
export let startInjections = async function() {
    if(typeof process === "undefined" || process.env.NODE_ENV != "test") {
        const { DataInjector } = await import("../../data/DataInjector.js");
        const { DomainInjector } = await import("../../domain/DomainInjector.js");
        const { PluginsInjector } = await import("../../plugins/PluginsInjector.js");
        const { PresenterInjector } = await import("../PresenterInjector.js");
        
        loadInjectors(
            PluginsInjector.getInstance(),
            DataInjector.getInstance(),
            DomainInjector.getInstance(),
            PresenterInjector.getInstance()
        );
    }
}