import { InteractiveSelfieCameraView } from "../../../selfie/ui/InteractiveSelfieCameraView.js";
import { MainCameraPresentation } from "../../presentation/api/MainCameraPresentation.js";
import { PassiveSelfieCameraView } from "../../../selfie/ui/PassiveSelfieCameraView.js";
import { DocumentCameraType, SelfieCameraType } from "../../../../../entities/CameraType.js"
import { DocumentCameraView } from "../../../document/DocumentCameraView.js";
import { startInjections } from "../../../../main/MainInjector.js";
import { get } from "../../../../../../di/DependencyInjector.js";
import { MainScreen } from "../api/MainScreen.js";
import { CameraOpener } from "../api/CameraOpener.js";

export class MainView extends MainScreen {

    /**
     * Camera View
     *
     * @param {Config} config
     * @param {Theme} theme
     */
    constructor(config, theme) {
        super(config, theme);

        const KEY_SELFIE = "SELFIE";

        /**
         * Camera opener
         */
        let cameraOpener;

        /**
         * MainCameraPresentation
         */
        let presenter;

        /**
         * Start main
         */
        let start = async function() {
            await startInjections();

            presenter = get(MainCameraPresentation);
        }

        /**
         * Prepare selfie camera
         *
         * @param {String} jsonPath
         * @param {SelfieCameraType} cameraType
         * @returns Promise
         */
        super.prepareSelfieCamera = async function(jsonPath, cameraType) {
            await start();

            let preparedCamera = await presenter.prepareCamera(config.origin, jsonPath);
            cameraOpener = buildCameraOpener(preparedCamera, cameraType);

            return cameraOpener;
        };

        /**
         * Prepare document camera
         *
         * @param {String} jsonPath
         * @param {DocumentCameraType} cameraType
         * @returns Promise
         */
        super.prepareDocumentCamera = async function(jsonPath, cameraType) {
            await start();

            let preparedCamera = await presenter.prepareCamera(config.origin, jsonPath)
            return buildCameraOpener(preparedCamera, cameraType);
        };

        /**
         * Get camera opener
         *
         * @param {Object} response
         * @param {CameraType} cameraType
         * @returns CameraOpener
         */
         let buildCameraOpener = function(response, cameraType) {
             var cameraOpener;

             if(cameraType instanceof SelfieCameraType) {
                 if (response.liveness.isEnabled) {
                     cameraOpener = openInteractiveCamera(response);
                } else {
                    cameraOpener = openPassiveCamera(
                        response.service.url,
                        response.service.auth,
                        response.service.session,
                        response.service.isBrandingEnabled,
                        cameraType
                    );
                }
            } else {
                cameraOpener = openDocumentCamera(
                    response.service.url,
                    response.service.auth,
                    response.service.session,
                    response.service.isBrandingEnabled,
                    cameraType
                );
            }

             return cameraOpener;
         }

        /**
         * Open Interactive camera
         *
         * @param {Object} info
         * @returns CameraOpener
         */
        let openInteractiveCamera = async function(info) {
            let facetecCamera = Object.assign(CameraOpener, InteractiveSelfieCameraView.getInstance(
                {
                    ...config,
                    ...{
                        productionKey: info.liveness.productionKey,
                        deviceKey: info.liveness.deviceKey,
                        publicKey: info.liveness.publicKey,
                        host: info.service.url,
                        apiKey: info.service.auth,
                        token: info.service.session,
                        isBrandingEnabled: info.service.isBrandingEnabled
                    }
                },
                theme
            ));

            await facetecCamera.initialize();

            return facetecCamera;
        }

        /**
         * Open passive camera
         *
         * @param {String} hostURL
         * @param {String} hostAPIKey
         * @param {Boolean} isBrandingEnabled
         * @param {SelfieCameraType} cameraType
         * @returns CameraOpener
         */
        let openPassiveCamera = async function(hostURL, hostAPIKey, token, isBrandingEnabled, cameraType) {
            let checkCamera = Object.assign(CameraOpener, PassiveSelfieCameraView.getInstance(
                {
                    ...config,
                    ...{
                        cameraType: cameraType,
                        token: token,
                        isBrandingEnabled: isBrandingEnabled
                     }
                },
                theme,
                hostURL,
                hostAPIKey
            ));

            await checkCamera.initialize();

            return checkCamera;
        }

        /**
         * Open document camera
         *
         * @param {String} hostURL
         * @param {String} hostAPIKey
         * @param {Boolean} isBrandingEnabled
         * @param {DocumentCameraType} cameraType
         * @returns CameraOpener
         */
         let openDocumentCamera = async function(hostURL, hostAPIKey, token, isBrandingEnabled, cameraType) {
            let checkCamera = Object.assign(CameraOpener, DocumentCameraView.getInstance(
                {
                    ...config,
                    ...{
                        cameraType: cameraType,
                        token: token,
                        isBrandingEnabled: isBrandingEnabled
                    }
                },
                theme,
                hostURL,
                hostAPIKey
            ));

            await checkCamera.initialize();

            return checkCamera;
        }
    }
}
