import { Encryptor } from "../api/Encryptor.js";
import { EncryptorData } from "../api/EncryptorData.js";
import { UnicoException } from "../../../entities/UnicoException.js";

export class EncryptorRepository extends EncryptorData {
    
    /**
     * Encryptor Repository
     * 
     * @param {Encryptor} encryptor 
     */
    constructor(encryptor, salt) {
        super();

        if (encryptor == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("encryptor");
        }

        if (!(encryptor instanceof Encryptor)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("encryptor", "Encryptor");
        }

        super.encrypt = function(obj, signature) {

            return new Promise((resolve) => {

                if (obj == undefined) {
                    throw new UnicoException.GENERAL_MISSING_PROPERTY("obj");
                }

                if (signature == undefined) {
                    throw new UnicoException.GENERAL_MISSING_PROPERTY("signature");
                }

                if (typeof signature !== "string") {
                    throw new UnicoException.GENERAL_INSTANCE_OF("signature", "String");
                }
    
                resolve(encryptor.encrypt(obj, signature, encryptor.algorithm(salt)));

            });
            
        }

        super.decrypt = function(encryptedText, signature) {

            return new Promise((resolve) => {
                
                if (encryptedText == undefined) {
                    throw new UnicoException.GENERAL_MISSING_PROPERTY("encryptedText");
                }

                if (typeof encryptedText !== "string") {
                    throw new UnicoException.GENERAL_INSTANCE_OF("encryptedText", "String");
                }

                if (signature == undefined) {
                    throw new UnicoException.GENERAL_MISSING_PROPERTY("signature");
                }

                if (typeof signature !== "string") {
                    throw new UnicoException.GENERAL_INSTANCE_OF("signature", "String");
                }

                resolve(encryptor.decrypt(encryptedText, signature, encryptor.algorithm(salt)));

            });
        }
    }
}