import { LivenessData } from "../api/LivenessData.js"
import { UnicoException } from "../../../entities/UnicoException.js";
import { UnicoHttpRequest } from "../../../plugins/networking/UnicoHttpRequest.js";

export class LivenessRepository extends LivenessData {

    /**
     * Liveness Repository
     * 
     * @param {UnicoHttpRequest} httpRequest 
     */
    constructor(httpRequest) {
        super();

        if(httpRequest == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("httpRequest");
        }

        if(!(httpRequest instanceof UnicoHttpRequest)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("httpRequest", "UnicoHttpRequest");
        }

        super.getSession = function(host, userAgent, apiKey, token) {

            if(host == null || host == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("host");
            }
            
            if(typeof host !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("host", "String");
            }
            
            if(userAgent == null || userAgent == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("userAgent");
            }
    
            if(typeof userAgent !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("userAgent", "String");
            }

            if(apiKey == null || apiKey == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("apiKey");
            }
            
            if(typeof apiKey !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("apiKey", "String");
            }

            if(token == null || token == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("token");
            }
            
            if(typeof token !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("token", "String");
            }

            return httpRequest.changeBaseURL(host).get(
                "/services/v3/acessoservice.svc/token/sdk/session",
                {
                    "Authorization": token,
                    "APIKEY": apiKey,
                    "x-user-agent": userAgent
                }
            );
        }
    }
}