export class MainScreen {

    /**
     * Main Screen
     * 
     * @param {Config} config 
     * @param {Theme} theme 
     */
    constructor(config, theme) {
        this.config = config;
        this.theme = theme;
    }

    /**
     * Prepare selfie camera
     * 
     * @abstract
     * @param {String} jsonPath
     * @param {SelfieCameraType} cameraType
     */
    prepareSelfieCamera(jsonPath, cameraType) {
        return Promise();
    }
    
    /**
     * Prepare document camera
     * 
     * @abstract
     * @param {String} jsonPath
     * @param {DocumentCameraType} unicoCameraType 
     * @returns Promise
     */
    prepareDocumentCamera(jsonPath, cameraType) {
        return Promise();
    }
}