import { Camera } from "../../../data/camera/api/Camera.js";
import { FaceTecStrings } from "./FacetecStrings.js";
import { FaceTecTheme } from "./FacetecTheme.js";
import { UnicoException } from "../../../entities/UnicoException.js";

export class FacetecCamera extends Camera {

    constructor() {
        super();

        var facetecSDK;

        super.initCamera = function(config, theme) {
            return new Promise((resolve, reject) => {
                if(facetecSDK == undefined) {
                    reject(new UnicoException.CAMERA_NOT_INITIALIZED);
                } else {
                    new facetecSDK.FaceTecSession({
                        onFaceTecSDKCompletelyDone: function() {

                        },
                        processSessionResultWhileFaceTecSDKWaits: function (sessionResult, faceScanResultCallback) {
                            if (sessionResult.status !== facetecSDK.FaceTecSessionStatus.SessionCompletedSuccessfully) {
                                if (
                                    sessionResult.status === facetecSDK.FaceTecSessionStatus.CameraNotEnabled ||
                                    sessionResult.status === facetecSDK.FaceTecSessionStatus.ContextSwitch
                                ) {
                                  faceScanResultCallback.cancel();
                                }
                                reject(faceTecErrorToUnicoException(sessionResult));
                                return;
                            }

                            faceScanResultCallback.succeed();
                            resolve(sessionResult);
                        }
                    }, config.session)
                }
            });
        }

        super.getUserAgent = function() {
            if(facetecSDK == undefined) {
                return ""
            } else {
                return facetecSDK.createFaceTecAPIUserAgentString("");
            }
        }

        super.loadModels = function(config, theme) {
            return new Promise(async (resolve, reject) => {
                try {
                    await loadScriptFile(`${config.cdn}/FaceTecSDK.js`);
                    facetecSDK = FaceTecSDK;
                    facetecSDK.setResourceDirectory(config.resourceDirectory);
                    facetecSDK.setCustomization(FaceTecTheme.getTheme(new facetecSDK.FaceTecCustomization(), theme, config.isBrandingEnabled));
                    facetecSDK.initializeInProductionMode(
                        config.productionKey,
                        config.deviceKey,
                        config.publicKey,
                        function (initializedSuccessfully) {
                            if(initializedSuccessfully) {
                                facetecSDK.configureLocalization(FaceTecStrings.getLocalized(config.locale));
                                resolve(facetecSDK);
                            } else {
                                reject(UnicoException.CAMERA_NOT_INITIALIZED);
                            }
                        }
                    );
                } catch (e) {
                    reject(UnicoException.CAMERA_RESOURCE_NOT_FOUND);
                }
            });
        }

        let loadScriptFile = function(url) {
            return new Promise((resolve, reject) => {
                var script = document.createElement("script")
                script.type = "text/javascript";

                if (script.readyState) {
                    script.onreadystatechange = function(){
                        if (script.readyState == "loaded" || script.readyState == "complete"){
                            script.onreadystatechange = null;
                            resolve();
                        }
                    };
                } else {
                    script.onload = function(){
                        resolve();
                    };

                    script.onerror = function(e) {
                        reject();
                    }
                }

                script.src = url;
                document.getElementsByTagName("head")[0].appendChild(script);
            });
        }

        let faceTecErrorToUnicoException = function(result) {
            switch(result.status) {
                case facetecSDK.FaceTecSessionStatus.MissingGuidanceImages:
                    return UnicoException.CAMERA_MISSING_GUIDANCE;
                case facetecSDK.FaceTecSessionStatus.Timeout:
                    return UnicoException.CAMERA_TIMEOUT;
                case facetecSDK.FaceTecSessionStatus.ContextSwitch:
                    return UnicoException.CAMERA_CONTEXT_SWITCH;
                case facetecSDK.FaceTecSessionStatus.ProgrammaticallyCancelled:
                    return UnicoException.CAMERA_PROGRAMMATICALLY_CANCELLED;
                case facetecSDK.FaceTecSessionStatus.OrientationChangeDuringSession:
                    return UnicoException.CAMERA_ORIENTATION_CHANGED;
                case facetecSDK.FaceTecSessionStatus.LandscapeModeNotAllowed:
                    return UnicoException.CAMERA_LANDSCAPE_NOT_ALLOWED;
                case facetecSDK.FaceTecSessionStatus.UserCancelled:
                    return UnicoException.CAMERA_USER_CANCELLED;
                case facetecSDK.FaceTecSessionStatus.UserCancelledFromNewUserGuidance:
                    return UnicoException.CAMERA_USER_CANCELLED;
                case facetecSDK.FaceTecSessionStatus.UserCancelledFromRetryGuidance:
                    return UnicoException.CAMERA_USER_CANCELLED;
                case facetecSDK.FaceTecSessionStatus.UserCancelledWhenAttemptingToGetCameraPermissions:
                    return UnicoException.CAMERA_USER_CANCELLED;
                case facetecSDK.FaceTecSessionStatus.LockedOut:
                    return UnicoException.CAMERA_LOCKED_OUT;
                case facetecSDK.FaceTecSessionStatus.CameraNotEnabled:
                    return UnicoException.CAMERA_PERMISSION;
                case facetecSDK.FaceTecSessionStatus.NonProductionModeDeviceKeyIdentifierInvalid:
                    return UnicoException.CAMERA_PRODUCTION_ERROR;
                case facetecSDK.FaceTecSessionStatus.DocumentNotReady:
                    return UnicoException.CAMERA_DOCUMENT_NOT_READY;
                case facetecSDK.FaceTecSessionStatus.SessionInProgress:
                    return UnicoException.CAMERA_SESSION_IN_PROGRESS;
                case facetecSDK.FaceTecSessionStatus.CameraNotRunning:
                    return UnicoException.CAMERA_NOT_RUNNING;
                case facetecSDK.FaceTecSessionStatus.InitializationNotCompleted:
                    return UnicoException.CAMERA_NOT_RUNNING;
                case facetecSDK.FaceTecSessionStatus.UnknownInternalError:
                    return UnicoException.CAMERA_UNKNOWN_ERROR;
                case facetecSDK.FaceTecSessionStatus.UserCancelledViaClickableReadyScreenSubtext:
                    return UnicoException.CAMERA_USER_CANCELLED;
                case facetecSDK.FaceTecSessionStatus.NotAllowedUseIframeConstructor:
                    return UnicoException.CAMERA_IFRAME_ERROR;
                case facetecSDK.FaceTecSessionStatus.NotAllowedUseNonIframeConstructor:
                    return UnicoException.CAMERA_IFRAME_ERROR;
                case facetecSDK.FaceTecSessionStatus.IFrameNotAllowedWithoutPermission:
                    return UnicoException.CAMERA_IFRAME_ERROR;
                case facetecSDK.FaceTecSessionStatus.StillLoadingResources:
                    return UnicoException.CAMERA_NOT_RUNNING;
                case facetecSDK.FaceTecSessionStatus.ResourcesCouldNotBeLoadedOnLastInit:
                    return UnicoException.CAMERA_NOT_RUNNING;
                case facetecSDK.FaceTecSessionStatus.UserCancelledFullScreenMode:
                    return UnicoException.CAMERA_USER_CANCELLED;
                default:
                    return UnicoException.CAMERA_UNKNOWN_ERROR;
            }
        }
    }
}
