import { UnicoException } from "../../../entities/UnicoException.js";
import { ValidateHexadecimalUseCase } from "../useCases/ValidateHexadecimalUseCase.js";

export class ValidateHexadecimalInteractor extends ValidateHexadecimalUseCase {

    /**
     * Validate a hexadecimal text
     */
    constructor() {
        super();
        
        super.execute = function(hexadecimal) {

            if(hexadecimal == null || hexadecimal == "" || hexadecimal === undefined) {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("hexadecimal");
            }
    
            if(typeof hexadecimal !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("hexadecimal", "String");
            }

            if(!/^#([0-9A-F]{3}){1,2}$/i.test(hexadecimal)) {
                throw UnicoException.THEME_HEXA_ERROR;
            }

            return true;
        }
    }
}