import * as acessoWebFrameTypes from "./frame-types.js"

function getLabelDocument(TYPE_PROCESS_DOCUMENT, LABEL_DOCUMENT_OTHERS) {
  if (TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH) {
    return "CNH Aberta";
  } else if (TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CPF) {
    return "CPF";
  }
  else if (TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH_FRONT) {
    return "CNH Frente";
  }
  else if (TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.CNH_BACK) {
    return "CNH Verso";
  } else if (
    TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_FRONT ||
    TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_FRONT
  ) {
    return "RG Frente";
  } else if (
    TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.RG_BACK ||
    TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_BACK
  ) {
    return "RG Verso";
  } else if (TYPE_PROCESS_DOCUMENT === acessoWebFrameTypes.TYPE_DOCUMENT.OTHERS) {
    return LABEL_DOCUMENT_OTHERS;
  }
  else {
    return "";
  }
}

export {
  getLabelDocument
}
