import { AuthenticatorData } from "../../../data/authenticator/api/AuthenticatorData.js"
import { UnicoException } from "../../../entities/UnicoException.js";
import { ValidateJsonUseCase } from "../useCases/ValidateJsonUseCase.js";

export class ValidateJsonInteractor extends ValidateJsonUseCase {

    /**
     * Validate local JSON
     * 
     * @param {AuthenticatorData} authenticatorRepository 
     * @returns Promise
     */
    constructor(authenticatorRepository) {
        super();

        const KEY_CLIENT_INFO = "client_info";
        const KEY_JS_CLIENT_INFO = "js_client_info";
        const KEY_HOSTNAME = "hostname";
        const KEY_HOST_INFO = "host_info";
        const KEY_HOST_KEY = "host_key"

        if(authenticatorRepository == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("authenticatorRepository");
        }

        if(!(authenticatorRepository instanceof AuthenticatorData)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("authenticatorRepository", "AuthenticatorData");
        }

        super.execute = function(origin, jsonPath) {
        
            if(origin == null || origin == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("origin");
            }
    
            if(typeof origin !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("origin", "String");
            }

            if(jsonPath == null || jsonPath == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("jsonPath");
            }

            if(typeof jsonPath !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("jsonPath", "String");
            }

            return new Promise(async (resolve, reject) => {
                var content;
                try {
                    content = await authenticatorRepository.loadJSON(jsonPath);
                } catch(e) {
                    if(e.hasOwnProperty("code") && e.code == 404) {
                        reject(UnicoException.JSON_NOT_FOUND);
                    } else {
                        reject(UnicoException.JSON_LOAD_ERROR);
                    }
                    return;
                }

                if(!validateJson(content)) {
                    reject(UnicoException.JSON_INVALID);
                    return;
                }
                
                if(validateHost(origin, content)) {
                    resolve(content.host_info);
                } else {
                    reject(UnicoException.AUTHENTICATION_INVALID_HOST);
                }
            });
        }

        let validateJson = function(jsonContent) {
            return validateJsonObject(jsonContent, KEY_CLIENT_INFO) &&
                validateJsonObject(jsonContent[KEY_CLIENT_INFO], KEY_JS_CLIENT_INFO) &&
                validateJsonString(jsonContent[KEY_CLIENT_INFO][KEY_JS_CLIENT_INFO], KEY_HOSTNAME) &&
                validateJsonObject(jsonContent, KEY_HOST_INFO) && 
                validateJsonString(jsonContent[KEY_HOST_INFO], KEY_HOST_INFO) &&
                validateJsonString(jsonContent[KEY_HOST_INFO], KEY_HOST_KEY);
        }

        let validateJsonObject = function(object, key) {
            return object.hasOwnProperty(key) &&
            typeof object[key] === "object" &&
            object[key] != null &&
            Object.values(object[key]).length > 0
        }

        let validateJsonString = function(object, key) {
            return object.hasOwnProperty(key) &&
            typeof object[key] === "string" &&
            object[key] != null &&
            object[key] != ""
        }

        let validateHost = function(origin, jsonContent) {

            const urlPattern = /https?:\/\//g;
            const registeredURL = new URL(origin);
            
            if(jsonContent.client_info.js_client_info.hostname == "*") {
                return true;
            } else if(urlPattern.test(jsonContent.client_info.js_client_info.hostname)) {
                const currentURL = new URL(jsonContent.client_info.js_client_info.hostname);
                return registeredURL.origin == currentURL.origin;
            } else {
                const currentURL = new URL(`http://${jsonContent.client_info.js_client_info.hostname}`)
                return registeredURL.hostname == currentURL.hostname;
            }

        }

    }

}