import { UnicoException } from "../../../entities/UnicoException.js";
import { System } from "../api/System.js";
import { SystemData } from "../api/SystemData.js";

export class SystemRepository extends SystemData {

    constructor(system) {
        super();

        if(system == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("system")
        }

        if(!(system instanceof System)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("system", "System")
        }

        super.getInfo = async function() {
            return {
                lib: {
                  version: await system.getVersion(),
                  date: await system.getCurrentDate()
                },
                device: {
                  ua: await system.getUA(),
                  model: await system.getModel(),
                  os: await system.getOS(),
                  platform: await system.getPlatform(),
                  api: await system.getAPI()
                },
                i_info: {
                  status: false,
                },
                url_domain: await system.getHost(),
                connections: await system.getConnection(),
              };
        }
    }
}
