import platform from "platform";
import { System } from "../../data/system/api/System.js";
import { version } from '../../../../package.json';

export class PlatformSystem extends System {

    constructor() {
        super();

        super.getAPI = async function() {
            return platform.os.family;
        }

        super.getConnection = async function() {
            const connection = {
                effectiveType: "",
                rtt: null,
                type: "",
                downlink: null
            };
          
            if (navigator.connection) {
                if (navigator.connection.effectiveType) {
                    connection.effectiveType = navigator.connection.effectiveType;
                }
                if (navigator.connection.rtt) {
                    connection.rtt = navigator.connection.rtt;
                }
                if (navigator.connection.type) {
                    connection.type = navigator.connection.type;
                }
                if (navigator.connection.downlink) {
                    connection.downlink = navigator.connection.downlink;
                }
            }
          
            return connection;
        }

        super.getCurrentDate = async function() {
            return Date.now();
        }

        super.getHost = async function() {
            return window.location.href;
        }

        super.getModel = async function() {
            return platform.product;
        }

        super.getOS = async function() {
            return platform.os.family;
        }

        super.getPlatform = async function() {
            let type_plataform = "js-";

            if(isIOS()){
              if(isMobile()){
                type_plataform += "ios";
              }
              else {
                type_plataform += "macos";
              }
            }
            else {
              if(isAndroid()){
                type_plataform += "android";
              }
              else {
                type_plataform += "windows";
              }
            }
          
            return type_plataform;
        }

        super.getUA = async function() {
            return platform.ua;
        }

        super.getVersion = async function() {
            return version;
        }

        let isMobile = function () {
            return navigator.userAgent.match(/Android/i) ||
                navigator.userAgent.match(/webOS/i) ||
                navigator.userAgent.match(/iPhone/i) ||
                navigator.userAgent.match(/iPad/i) ||
                navigator.userAgent.match(/iPod/i) ||
                navigator.userAgent.match(/BlackBerry/i) ||
                navigator.userAgent.match(/Windows Phone/i)
                ? true
                : false;
        }

        let isIOS = function() {
            return navigator.userAgent.match(/webOS/i) ||
                navigator.userAgent.match(/iPhone/i) ||
                navigator.userAgent.match(/iPad/i) ||
                navigator.userAgent.match(/iPod/i) ||
                navigator.userAgent.match(/Mac OS/i)
                ? true
                : false;
        }

        let isAndroid = function() {
            return navigator.userAgent.match(/Android/i) ? true : false;
        }
    }
}