import { SelfieCameraTypes, DocumentCameraTypes } from "../../entities/CameraType.js";
import { LocaleTypes } from "../../entities/LocaleType.js";
import { MainView } from "../camera/main/ui/impl/MainView.js";
import { UnicoTheme } from "../themes/UnicoTheme.js";
import { UnicoThemeBuilder } from "../themes/UnicoThemeBuilder.js";
import { UnicoException } from "../../entities/UnicoException.js";
import { UnicoThemeDefault } from "../themes/UnicoThemeDefault.js";

const UnicoCheckBuilder = class UnicoCheckBuilder {

    /**
     * Builder of UnicoCamera
     * 
     * @param {String} origin 
     */
    constructor(origin = window.location.origin) {
        
        /**
         * @readonly
         */
        var _config = {
            origin: origin
        };

        /**
         * @readonly
         */
        var _theme = {};

        /**
         * Set camera theme
         * 
         * @param {UnicoTheme} theme 
         * @returns UnicoCheckBuilder
         */
        this.setTheme = function (theme) {
            if(theme instanceof UnicoTheme){

                const themeDefault = UnicoThemeDefault.get();
                _theme = UnicoThemeDefault.merge(theme, themeDefault);
            
                return this;
            }
            else {
                throw UnicoException.THEME_ERROR;
            }
            
        }

        /**
         * Set locale
         * 
         * @param {LocaleType} locale 
         * @returns UnicoCheckBuilder
         */
         this.setLocale = function (locale) {
            _config = { ..._config, ...{ locale: locale } };
            
            return this;
        }

        /**
         * Set ML models path for Smart Camera
         * 
         * @param {String} urlPathModels 
         * @returns UnicoCheckBuilder
         */
        this.setModelsPath = function(urlPathModels) {
            _config = { ..._config, ...{ models: urlPathModels } };
            
            return this;
        }

        /**
         * Set resources directory for Interactive Camera
         * @param {String} relativePath 
         * @returns 
         */
        this.setResourceDirectory = function(relativePath) {
            _config = { ..._config, ...{ resourceDirectory: relativePath } };
            
            return this;
        }

        /**
         * Build a MainView
         * 
         * @returns MainView
         */
        this.build = function() {

            if(!_config.hasOwnProperty("locale")) {
                _config["locale"] = LocaleTypes.PT_BR
            }

            if(_theme instanceof UnicoTheme === false) {
                _theme = UnicoThemeDefault.get();
            }

            return new MainView(_config, _theme);
        }
    }
}

export { 
    UnicoCheckBuilder,
    UnicoThemeBuilder,
    DocumentCameraTypes,
    SelfieCameraTypes,
    LocaleTypes
}