export function AuthenticatorData() { }

/**
 * Load JSON file
 *
 * @abstract
 * @param {String} jsonPath
 * @returns Promise
 */
AuthenticatorData.prototype.loadJSON = (jsonPath) => {
  return Promise();
}

/**
 * Authenticate
 *
 * @abstract
 * @param {String} host
 * @param {String} apiKey
 * @param {String} origin
 * @returns Promise
 */
AuthenticatorData.prototype.authenticate = (host, apiKey, origin) => {
  return Promise();
}
