import { AuthenticatorData } from "../data/authenticator/api/AuthenticatorData.js";
import { AuthenticateInteractor } from "./authenticator/interactors/AuthenticateInteractor.js";
import { AuthenticateUseCase } from "./authenticator/useCases/AuthenticateUseCase.js";
import { CameraData } from "../data/camera/api/CameraData.js";
import { Config } from "../modules/config/Config.js";
import { DecryptInteractor } from "./encryptor/interactor/DecryptInteractor.js";
import { DecryptUseCase } from "./encryptor/useCases/DecryptUseCase.js";
import { EncryptInteractor } from "./encryptor/interactor/EncryptInteractor.js";
import { EncryptorData } from "../data/encryptor/api/EncryptorData.js";
import { EncryptUseCase } from "./encryptor/useCases/EncryptUseCase.js";
import { GetSessionUseCase } from "./liveness/useCases/GetSessionUseCase.js";
import { GetSessionInteractor } from "./liveness/interactors/GetSessionInteractor.js";
import { Injector } from "../../di/Injector.js";
import { LivenessData } from "../data/liveness/api/LivenessData.js";
import { LoadModelInteractor } from "./camera/interactors/LoadModelInteractor.js"
import { LoadModelUseCase } from "./camera/useCases/LoadModelUseCase.js";
import { ValidateJsonInteractor } from "./security/interactors/ValidateJsonInteractor.js";
import { ValidateJsonUseCase } from "./security/useCases/ValidateJsonUseCase.js";
import { get, singleton } from "../../di/DependencyInjector.js";
import { GetSystemInfoUseCase } from "./system/useCases/GetSystemInfoUseCase.js";
import { GetSystemInfoInteractor } from "./system/interactors/GetSystemInfoInteractor.js";
import { SystemData } from "../data/system/api/SystemData.js";
import { ValidateHexadecimalUseCase } from "./validators/useCases/ValidateHexadecimalUseCase.js";
import { ValidateHexadecimalInteractor } from "./validators/interactor/ValidateHexadecimalInteractor.js";

const DomainInjectorFactory = (function() {
    var INSTANCE;

    class DomainInjector extends Injector {
        constructor() {
            super();

            super.inject = function() {
                
                singleton(AuthenticateUseCase, function() {
                    return new AuthenticateInteractor(get(AuthenticatorData, "REMOTE"));
                });

                singleton(DecryptUseCase, function() {
                    return new DecryptInteractor(get(EncryptorData, "API"), Config.RIJNDAEL_KEY_CODE);
                });

                singleton(EncryptUseCase, function() {
                    return new EncryptInteractor(get(EncryptorData, "CLIENT"));
                });

                singleton(GetSessionUseCase, function() {
                    return new GetSessionInteractor(get(LivenessData));
                });

                singleton(LoadModelUseCase, function() {
                    return new LoadModelInteractor(get(CameraData));
                })

                singleton(ValidateJsonUseCase, function() {
                    return new ValidateJsonInteractor(get(AuthenticatorData, "LOCAL"));
                });

                singleton(GetSystemInfoUseCase, function(){
                    return new GetSystemInfoInteractor(get(SystemData));
                });

                singleton(ValidateHexadecimalUseCase, function(){
                    return new ValidateHexadecimalInteractor();
                });
            }
        }
    }

    return {
        getInstance: function(){
            if (INSTANCE == null) {
                INSTANCE = new DomainInjector();
                INSTANCE.constructor = null;
            }
            return INSTANCE;
        }
    };
})();

export var DomainInjector = DomainInjectorFactory;