import { EncryptorData } from "../../../data/encryptor/api/EncryptorData.js"
import { EncryptUseCase } from "../useCases/EncryptUseCase.js";
import { UnicoException } from "../../../entities/UnicoException.js";

export class EncryptInteractor extends EncryptUseCase {

    /**
     * Encrypt anything
     * 
     * @param {EncryptorData} encryptorRepository 
     */
    constructor(encryptorRepository) {
        super();

        if(encryptorRepository == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("encryptorRepository");
        }

        if(!(encryptorRepository instanceof EncryptorData)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("encryptorRepository", "EncryptorData");
        }
        
        super.execute = function(obj, signature) {

            if(obj === undefined || obj == null || obj == "" || ( typeof obj == "object" && Object.values(obj).length < 1 )) {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("obj");
            }

            if(signature == null || signature == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("signature");
            }
    
            if(typeof signature !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("signature", "String");
            }

            return encryptorRepository.encrypt(obj, signature);
        }
    }
}