export const FaceTecTheme = (function () {
    return {
        getTheme: function (customization, unicoTheme, isBrandingEnabled = false) {

            //silhouette
            customization.ovalCustomization.strokeColor = unicoTheme.colorSilhouetteNeutral;
            customization.ovalCustomization.progressColor1 = unicoTheme.colorSilhouetteSuccess;
            customization.ovalCustomization.progressColor2 = unicoTheme.colorSilhouetteSuccess;
            //background
            customization.overlayCustomization.backgroundColor = unicoTheme.backgroundColor;
            customization.frameCustomization.backgroundColor = unicoTheme.backgroundColor;

            //backgroundcolor text guid prepare to capture and other steps
            customization.guidanceCustomization.backgroundColors = unicoTheme.backgroundColor;

            //init loading
            customization.initialLoadingAnimationCustomization.backgroundColor = unicoTheme.backgroundColorComponents;
            customization.initialLoadingAnimationCustomization.foregroundColor = unicoTheme.colorTextComponents;

            //disable audio
            customization.vocalGuidanceCustomization.mode = 2;
            //result screen
            customization.resultScreenCustomization.backgroundColors = unicoTheme.backgroundColor;
            customization.resultScreenCustomization.foregroundColor = unicoTheme.colorText;
            customization.resultScreenCustomization.activityIndicatorColor = unicoTheme.backgroundColorComponents;
            customization.resultScreenCustomization.resultAnimationBackgroundColor = unicoTheme.backgroundColorComponents;
            customization.resultScreenCustomization.resultAnimationForegroundColor = unicoTheme.colorTextComponents;
            customization.resultScreenCustomization.uploadProgressFillColor = unicoTheme.backgroundColorComponents;
            customization.resultScreenCustomization.uploadProgressTrackColor = unicoTheme.colorTextComponents;

            //button
            customization.guidanceCustomization.buttonBackgroundNormalColor = unicoTheme.backgroundColorButtons;
            customization.guidanceCustomization.buttonBackgroundHighlightColor = unicoTheme.backgroundColorButtons;
            customization.guidanceCustomization.buttonBackgroundDisabledColor = unicoTheme.backgroundColorButtons;
            customization.guidanceCustomization.buttonTextNormalColor = unicoTheme.colorTextButtons;
            customization.guidanceCustomization.buttonTextDisabledColor = unicoTheme.colorTextButtons;
            customization.guidanceCustomization.buttonTextHighlightColor = unicoTheme.colorTextButtons;

            //box message
            customization.feedbackCustomization.backgroundColor = unicoTheme.backgroundColorBoxMessage;
            customization.feedbackCustomization.textColor = unicoTheme.colorTextBoxMessage;

            //text
            //color text guid prepare to capture
            customization.guidanceCustomization.foregroundColor = unicoTheme.colorText;

            //when the client have to try again
            customization.guidanceCustomization.retryScreenHeaderTextColor = unicoTheme.colorText;
            customization.guidanceCustomization.retryScreenSubtextTextColor = unicoTheme.colorText;
            //init loading text color
            customization.initialLoadingAnimationCustomization.messageTextColor = unicoTheme.colorText;

            customization.guidanceCustomization.readScreenHeaderTextColor = unicoTheme.colorText;
            customization.guidanceCustomization.readScreenSubtextColor = unicoTheme.colorText;

            //border color
            customization.guidanceCustomization.retryScreenImageBorderColor = unicoTheme.colorText;
            customization.guidanceCustomization.buttonBorderColor = unicoTheme.colorText;
            customization.enterFullScreenCustomization.buttonBorderColor = unicoTheme.colorText;
            customization.enterFullScreenCustomization.borderColor = unicoTheme.colorText;
            customization.frameCustomization.borderColor = unicoTheme.colorText;
            //cancel button
            customization.cancelButtonCustomization.location = 0;

            //remove fullscreen mode
            customization.overlayCustomization.showBrandingImage = isBrandingEnabled;
            customization.guidanceCustomization.enableRetryScreenSlideshowShuffle = true;
            customization.guidanceCustomization.buttonBorderWidth = '0px';
            customization.guidanceCustomization.buttonCornerRadius = '30px';
            customization.guidanceCustomization.readyScreenTextBackgroundCornerRadius = '5px';
            customization.guidanceCustomization.retryScreenImageBorderWidth = '2px';
            customization.guidanceCustomization.retryScreenImageCornerRadius = '10px';
            customization.guidanceCustomization.retryScreenSlideshowInterval = '2000ms';
            customization.resultScreenCustomization.customActivityIndicatorRotationInterval = '800s';
            customization.resultScreenCustomization.animationRelativeScale = 1;
            customization.feedbackCustomization.cornerRadius = '5px';
            customization.frameCustomization.borderWidth = '0px';
            customization.frameCustomization.borderCornerRadius = '0px';

            //help screen camera permissions
            customization.enableCameraPermissionsHelpScreen = false;

            // branding
            let svg = `<svg width="334" height="68" viewBox="0 0 334 68" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M151.343 21.4106H147.893V39.9999H151.343V21.4106Z" fill="url(#paint0_linear_419_731)"/>
            <path d="M167.072 39.9999H163.645V30.9661C163.645 27.5755 160.885 24.8487 157.506 24.8487C154.103 24.8487 151.367 27.5992 151.367 30.9661V39.9999H147.893V30.9661C147.893 25.7023 152.2 21.4106 157.482 21.4106C162.765 21.4106 167.072 25.7023 167.072 30.9661V39.9999Z" fill="url(#paint1_linear_419_731)"/>
            <path d="M136.59 39.9999C131.307 39.9999 127 35.7083 127 30.4445V21.4106H130.45V30.4445C130.45 33.8351 133.211 36.5619 136.59 36.5619C139.968 36.5619 142.729 33.8114 142.729 30.4445V21.4106H146.179V30.4445C146.179 35.7083 141.872 39.9999 136.59 39.9999Z" fill="url(#paint2_linear_419_731)"/>
            <path d="M172.235 21.4106H168.785V39.9999H172.235V21.4106Z" fill="url(#paint3_linear_419_731)"/>
            <path d="M197.672 39.9999C192.532 39.9999 188.344 35.8268 188.344 30.7053C188.344 25.5838 192.532 21.4106 197.672 21.4106C202.812 21.4106 207 25.5838 207 30.7053C207 35.8268 202.812 39.9999 197.672 39.9999ZM197.672 24.8487C194.436 24.8487 191.795 27.4806 191.795 30.7053C191.795 33.93 194.436 36.5619 197.672 36.5619C200.908 36.5619 203.549 33.93 203.549 30.7053C203.549 27.4569 200.908 24.8487 197.672 24.8487Z" fill="url(#paint4_linear_419_731)"/>
            <path d="M183.276 39.9999C178.136 39.9999 173.948 35.8268 173.948 30.7053C173.948 25.5838 178.136 21.4106 183.276 21.4106C185.18 21.4106 187.012 21.9797 188.582 23.0704L186.631 25.892C185.656 25.2044 184.49 24.8487 183.276 24.8487C180.04 24.8487 177.399 27.4806 177.399 30.7053C177.399 33.93 180.04 36.5619 183.276 36.5619C184.49 36.5619 185.632 36.2062 186.631 35.5186L188.582 38.3402C187.036 39.4309 185.18 39.9999 183.276 39.9999Z" fill="url(#paint5_linear_419_731)"/>
            <line x1="4.37114e-08" y1="0.910645" x2="334" y2="0.910674" stroke="#DCDCDC"/>
            <defs>
            <linearGradient id="paint0_linear_419_731" x1="149.618" y1="40.357" x2="149.618" y2="20.1661" gradientUnits="userSpaceOnUse">
            <stop stop-color="#00A5FF"/>
            <stop offset="1" stop-color="#CF0080"/>
            </linearGradient>
            <linearGradient id="paint1_linear_419_731" x1="124.899" y1="30.7014" x2="206.564" y2="30.7014" gradientUnits="userSpaceOnUse">
            <stop stop-color="#00A5FF"/>
            <stop offset="1" stop-color="#CF0080"/>
            </linearGradient>
            <linearGradient id="paint2_linear_419_731" x1="124.899" y1="30.7014" x2="206.564" y2="30.7014" gradientUnits="userSpaceOnUse">
            <stop stop-color="#00A5FF"/>
            <stop offset="1" stop-color="#CF0080"/>
            </linearGradient>
            <linearGradient id="paint3_linear_419_731" x1="124.899" y1="30.7014" x2="206.564" y2="30.7014" gradientUnits="userSpaceOnUse">
            <stop stop-color="#00A5FF"/>
            <stop offset="1" stop-color="#CF0080"/>
            </linearGradient>
            <linearGradient id="paint4_linear_419_731" x1="124.899" y1="30.7014" x2="206.564" y2="30.7014" gradientUnits="userSpaceOnUse">
            <stop stop-color="#00A5FF"/>
            <stop offset="1" stop-color="#CF0080"/>
            </linearGradient>
            <linearGradient id="paint5_linear_419_731" x1="124.899" y1="30.7014" x2="206.564" y2="30.7014" gradientUnits="userSpaceOnUse">
            <stop stop-color="#00A5FF"/>
            <stop offset="1" stop-color="#CF0080"/>
            </linearGradient>
            </defs>
            </svg>`;

            let blob = new Blob([svg], { type: 'image/svg+xml' });
            customization.overlayCustomization.brandingImage = URL.createObjectURL(blob);

            return customization;
        }
    };
})();
