import { CameraData } from "../../../data/camera/api/CameraData.js"
import { UnicoException } from "../../../entities/UnicoException.js";
import { LoadModelUseCase } from "../useCases/LoadModelUseCase.js";

export class LoadModelInteractor extends LoadModelUseCase {

    /**
     * Load AI Models
     * 
     * @param {CameraData} cameraRepository 
     */
    constructor(cameraRepository) {
        super();

        if(cameraRepository == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("cameraRepository");
        }

        if(!(cameraRepository instanceof CameraData)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("cameraRepository", "CameraData");
        }

        super.execute = function(modelsPath) {

            if(modelsPath == null || modelsPath == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("modelsPath");
            }
    
            if(typeof modelsPath !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("modelsPath", "String");
            }

            return cameraRepository.loadCameraModels(modelsPath);
        }
    }
}