export function SelfieCameraPresentation() { }

/**
 * Initialize camera
 *
 * @abstract
 * @param {String} filePath
 * @returns
 */
SelfieCameraPresentation.prototype.initialize = (filePath) => {
  return Promise();
};

/**
 * Get camera session
 *
 * @abstract
 * @param {String} host
 * @param {String} userAgent
 * @param {String} apiKey
 * @param {String} token
 * @returns Promise
 */
SelfieCameraPresentation.prototype.getCameraSession = (host, userAgent, apiKey, token) => {
  return Promise();
};

/**
 * Get system info
 * @abstract
 * @retunrs Promise
 */
SelfieCameraPresentation.prototype.getSystemInfo = () => {
  return Promise();
}

/**
 * Encrypt result
 *
 * @abstract
 * @param {*} result
 * @param {String} signature
 * @returns Promise
 */
SelfieCameraPresentation.prototype.encrypt = async (result, signature) => {
  return Promise();
}

/**
 * Validate result
 *
 * @abstract
 * @param {*} result
 * @returns Promise
 */
SelfieCameraPresentation.prototype.validate = async (result) => {
  return Promise();
}

