import { GetSessionUseCase } from "../useCases/GetSessionUseCase.js";
import { LivenessData } from "../../../data/liveness/api/LivenessData.js"
import { UnicoException } from "../../../entities/UnicoException.js";

export class GetSessionInteractor extends GetSessionUseCase {

    /**
     * Get liveness camera session
     * 
     * @param {LivenessData} livenessRepository 
     */
    constructor(livenessRepository) {
        super();

        if(livenessRepository == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("livenessRepository");
        }

        if(!(livenessRepository instanceof LivenessData)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("livenessRepository", "LivenessData");
        }
        
        super.execute = function(host, userAgent, apiKey, token) {

            if(host == null || host == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("host");
            }
    
            if(typeof host !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("host", "String");
            }

            if(userAgent == null || userAgent == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("userAgent");
            }
    
            if(typeof userAgent !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("userAgent", "String");
            }

            if(apiKey == null || apiKey == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("apiKey");
            }
    
            if(typeof apiKey !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("apiKey", "String");
            }

            if(token == null || token == "") {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("token");
            }
    
            if(typeof token !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("token", "String");
            }

            return livenessRepository.getSession(host, userAgent, apiKey, token);
        }
    }
}