export class HttpRequestTypes {
	
	constructor(name) {
	  this.name = name;
	}

	toString() {
	  return `UnicoHttpRequestType.${this.name}`;
	}
}

export const UnicoHttpRequestType = {
	GET: new HttpRequestTypes('GET'),
	POST: new HttpRequestTypes('POST'),
	PATCH: new HttpRequestTypes('PATCH'),
	PUT: new HttpRequestTypes('PUT'),
	DELETE: new HttpRequestTypes('DELETE')
}