import { AuthenticateUseCase } from "../domain/authenticator/useCases/AuthenticateUseCase.js";
import { DecryptUseCase } from "../domain/encryptor/useCases/DecryptUseCase.js";
import { EncryptUseCase } from "../domain/encryptor/useCases/EncryptUseCase.js";
import { GetSessionUseCase } from "../domain/liveness/useCases/GetSessionUseCase.js";
import { Injector } from "../../di/Injector.js"
import { InteractiveSelfieCameraPresenter } from "./camera/selfie/presentation/impl/InteractiveSelfieCameraPresenter.js";
import { LoadModelUseCase } from "../domain/camera/useCases/LoadModelUseCase.js";
import { MainCameraPresentation } from "./camera/main/presentation/api/MainCameraPresentation.js";
import { MainCameraPresenter } from "./camera/main/presentation/impl/MainCameraPresenter.js";
import { PassiveSelfieCameraPresenter } from "./camera/selfie/presentation/impl/PassiveSelfieCameraPresenter.js";
import { SelfieCameraPresentation } from "./camera/selfie/presentation/api/SelfieCameraPresentation.js";
import { ValidateJsonUseCase } from "../domain/security/useCases/ValidateJsonUseCase.js";
import { get, singleton } from "../../di/DependencyInjector.js";
import { GetSystemInfoUseCase } from "../domain/system/useCases/GetSystemInfoUseCase.js";

const PresenterInjectorFactory = (function() {
    var INSTANCE;

    class PresenterInjector extends Injector {
        constructor() {
            super();

            super.inject = function() {
                
                singleton(MainCameraPresentation, function() {
                    return new MainCameraPresenter(get(ValidateJsonUseCase), get(DecryptUseCase), get(AuthenticateUseCase));
                });

                singleton(SelfieCameraPresentation, function() {
                    return new PassiveSelfieCameraPresenter(get(AuthenticateUseCase), get(LoadModelUseCase), get(EncryptUseCase));
                }, "PASSIVE");

                singleton(SelfieCameraPresentation, function() {
                    return new InteractiveSelfieCameraPresenter(get(GetSessionUseCase), get(EncryptUseCase), get(GetSystemInfoUseCase));
                }, "INTERACTIVE");

            }
        }
    }

    return {
        getInstance: function(){
            if (INSTANCE == null) {
                INSTANCE = new PresenterInjector();
                INSTANCE.constructor = null;
            }
            return INSTANCE;
        }
    };
})();

export var PresenterInjector = PresenterInjectorFactory;