import { UnicoTheme } from "./UnicoTheme.js";
import { get } from "../../../di/DependencyInjector.js";
import { ValidateHexadecimalUseCase } from "../../domain/validators/useCases/ValidateHexadecimalUseCase.js";
import { ValidateHexadecimalInteractor } from "../../domain/validators/interactor/ValidateHexadecimalInteractor.js";
import { UnicoException } from "../../entities/UnicoException.js";

const UnicoThemeBuilder = class UnicoThemeBuilder {

    /**
     * Builder of UnicoTheme
     */
    constructor() {

        let validateHexadecimalUseCase;

        if(typeof process === "undefined" || process.env.NODE_ENV != "test") { 
            validateHexadecimalUseCase = new ValidateHexadecimalInteractor();
        }
        else {
            validateHexadecimalUseCase = get(ValidateHexadecimalUseCase);
        }

        let colorSilhouetteNeutral = null; 
        let colorSilhouetteSuccess = null;
        let colorSilhouetteError = null;
        let backgroundColor = null;
        let colorText = null;
        let backgroundColorComponents = null;
        let colorTextComponents = null;
        let backgroundColorBoxMessage = null;
        let colorTextBoxMessage = null;
        let backgroundColorButtons = null;
        let colorTextButtons = null;
        let htmlPopupLoading = null;

        /**
         * Set ColorSilhouetteSuccess config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
        this.setColorSilhouetteSuccess = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorSilhouetteSuccess = color;
            
            return this;
        }

        /**
         * Set ColorSilhouetteError config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
        this.setColorSilhouetteError = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorSilhouetteError = color;

            return this;
        }

        /**
         * Set ColorSilhouetteNeutral config
         * 
         * @param {string} color ((hexadecimal))
         * @returns UnicoThemeBuilder
         */
        this.setColorSilhouetteNeutral = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorSilhouetteNeutral = color;

            return this;
        }

        /**
         * Set BackgroundColor config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
        this.setBackgroundColor = function (color) {
            validateHexadecimalUseCase.execute(color);
            backgroundColor = color;

            return this;
        }

        /**
         * Set ColorText config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setColorText = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorText = color;

            return this;
        }

        /**
         * Set BackgroundColorComponents config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setBackgroundColorComponents = function (color) {
            validateHexadecimalUseCase.execute(color);
            backgroundColorComponents = color;

            return this;
        }

        /**
         * Set ColorTextComponents config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setColorTextComponents = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorTextComponents = color;

            return this;
        }

        /**
         * Set BackgroundColorButtons config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setBackgroundColorButtons = function (color) {
            validateHexadecimalUseCase.execute(color);
            backgroundColorButtons = color;

            return this;
        }

        /**
         * Set ColorTextButtons config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setColorTextButtons = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorTextButtons = color;

            return this;
        }

        /**
         * Set BackgroundColorBoxMessage config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setBackgroundColorBoxMessage = function (color) {
            validateHexadecimalUseCase.execute(color);
            backgroundColorBoxMessage = color;

            return this;
        }

        /**
         * Set ColorTextBoxMessage config
         * 
         * @param {string} color (hexadecimal)
         * @returns UnicoThemeBuilder
         */
         this.setColorTextBoxMessage = function (color) {
            validateHexadecimalUseCase.execute(color);
            colorTextBoxMessage = color;

            return this;
        }

        /**
         * Set HtmlPopupLoading config
         * 
         * @param {string} html
         * @returns UnicoThemeBuilder
         */
         this.setHtmlPopupLoading = function (html) {

            if(html == null || html === undefined) {
                throw new UnicoException.GENERAL_MISSING_PROPERTY("html");
            }
    
            if(typeof html !== "string") {
                throw new UnicoException.GENERAL_INSTANCE_OF("html", "String");
            }

            htmlPopupLoading = html;

            return this;
        }

        /**
         * Build a UnicoTheme
         * 
         * @returns UnicoTheme
         */
        this.build = function() {
            return new UnicoTheme(
                colorSilhouetteNeutral,
                colorSilhouetteSuccess,
                colorSilhouetteError,
                backgroundColor,
                colorText,
                backgroundColorComponents, 
                colorTextComponents,
                backgroundColorBoxMessage,
                colorTextBoxMessage,
                backgroundColorButtons,
                colorTextButtons,
                htmlPopupLoading
            );
        }
    }
}

export { 
    UnicoThemeBuilder
}