import { Camera } from "../../../../data/camera/api/Camera.js";
import { CameraOpener } from "../../main/ui/api/CameraOpener.js";
import { SelfieCameraPresentation } from "../presentation/api/SelfieCameraPresentation.js";
import { get } from "../../../../../di/DependencyInjector.js";

const InteractiveSelfieCameraViewFactory = (function(){

    class InteractiveSelfieCameraView extends CameraOpener {

        /**
         * Interactive selfie camera
         *
         * @param {Config} config
         * @param {Theme} theme
         * @param {SelfieCameraPresentation} presenter
         * @param {Camera} camera
         */
        constructor(config, theme, presenter, camera) {
            super(config, theme, presenter, camera);

            const baseURL = "aHR0cHM6Ly9jZG4udW5pY28uaW8vc2RrL2NoZWNrL2ZhY2V0ZWM="

            super.initialize = function () {
                return camera.loadModels(Object.assign({ cdn: window.atob(baseURL) }, config), theme);
            }

            super.open = async function (listener) {
                let userAgent;
                try {
                    userAgent = camera.getUserAgent();
                } catch(e) {
                    userAgent = ""
                }

                return presenter
                    .getCameraSession(config.host, userAgent, config.apiKey, config.token)
                    .then(session => {
                        return camera.initCamera(Object.assign({ session: session }, config), listener, theme);
                    })
                    .then(async (result) => {
                        result["info"] = await presenter.getSystemInfo();
                        return result;
                    })
                    .then(result => {
                        return presenter.validate(result);
                    })
                    .then(async (result) => {
                        result["encrypted"] = await presenter.encrypt({
                            faceScan: result.faceScan,
                            lowQualityAuditTrailImage: result.lowQualityAuditTrail[0],
                            auth: config.token,
                            image: result.auditTrail[0],
                            info: result.info
                        }, window.btoa(config.token));

                        return result;
                    })
                    .then(result => {
                        return {
                            base64: result.auditTrail[0],
                            encrypted: result.encrypted
                        }
                    })
                    .then(response => {
                        listener.on.success(response);
                    })
                    .catch(e => {
                        listener.on.error(e);
                    });
            };
        }
    }

    return {
        getInstance: function(config, theme){
            return new InteractiveSelfieCameraView(
                config,
                theme,
                get(SelfieCameraPresentation, "INTERACTIVE"),
                get(Camera, "FACETEC")
            );

        }
   };
})();

export var InteractiveSelfieCameraView = InteractiveSelfieCameraViewFactory
