import { AuthenticateUseCase } from "../../../../../domain/authenticator/useCases/AuthenticateUseCase.js";
import { DecryptUseCase } from "../../../../../domain/encryptor/useCases/DecryptUseCase.js";
import { MainCameraPresentation } from "../api/MainCameraPresentation.js";
import { UnicoException } from "../../../../../entities/UnicoException.js";
import { ValidateJsonUseCase } from "../../../../../domain/security/useCases/ValidateJsonUseCase.js";

export class MainCameraPresenter extends MainCameraPresentation {

    /**
     * Implementation of UnicoCamera
     */
    constructor(validateJsonUseCase, decryptUseCase, authenticateUseCase) {
        super();

        const DEVICE_KEY = "Dk";
        const PUBLIC_KEY = "Ec";
        const PRODUCTION_KEY = "BrowserPk";
        const TOKEN_KEY = "Token";
        const BRANDING_KEY = "EnableLogo";

        if(validateJsonUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("validateJsonUseCase");
        }

        if(!(validateJsonUseCase instanceof ValidateJsonUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("validateJsonUseCase", "ValidateJsonUseCase");
        }

        if(decryptUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("decryptUseCase");
        }

        if(!(decryptUseCase instanceof DecryptUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("decryptUseCase", "DecryptUseCase");
        }

        if(authenticateUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("authenticateUseCase");
        }

        if(!(authenticateUseCase instanceof AuthenticateUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("authenticateUseCase", "AuthenticateUseCase");
        }
        
        super.prepareCamera = function(origin, jsonPath) {
            return new Promise(async (resolve, reject) => {
                var jsonContent, hostURL, hostAPIKey;
                
                try {
                    jsonContent = await validateJsonUseCase.execute(origin, jsonPath);
                } catch (e) {
                    reject(e);
                    return;
                }

                try {
                    hostURL = await decryptUseCase.execute(jsonContent.host_info);
                    hostAPIKey = await decryptUseCase.execute(jsonContent.host_key);                    
                } catch (e) {   
                    reject(UnicoException.AUTHENTICATION_PARSE_ERROR);
                    return;
                }

                try {
                    resolve(getCameraInfo(hostURL, hostAPIKey, origin));
                } catch(e) {
                    reject(e);
                }
            });
        }
    
        /**
         * Get camera info
         * 
         * @param {String} hostURL 
         * @param {String} hostAPIKey
         * @param {String} origin
         */
        let getCameraInfo = async function(hostURL, hostAPIKey, origin) {
            var session;
            try {
                session = await authenticateUseCase.execute(hostURL, hostAPIKey, origin);
            } catch(e) {
                throw UnicoException.AUTHENTICATION_REQUEST_ERROR;
            }

            if(!validateSession(session)) {
                throw UnicoException.AUTHENTICATION_TOKEN_NOT_FOUND;
            }

            return {
                service: {
                    url: hostURL,
                    auth: hostAPIKey,
                    session: session[TOKEN_KEY],
                    isBrandingEnabled: (session[BRANDING_KEY] === undefined) ? false : session[BRANDING_KEY],
                },
                liveness: {
                    isEnabled: isFacetecEnabled(session),
                    deviceKey: isFacetecEnabled(session) ? session[DEVICE_KEY] : null,
                    publicKey: isFacetecEnabled(session) ? session[PUBLIC_KEY] : null,
                    productionKey: isFacetecEnabled(session) ? session[PRODUCTION_KEY] : null,
                }
            }
        }

        /**
         * Check if facetec is enabled
         * 
         * @param {Object} sessionContent 
         * @returns Boolean
         */
        let isFacetecEnabled = function(sessionContent) {
            return sessionContent.hasOwnProperty(DEVICE_KEY) && 
                sessionContent[DEVICE_KEY] != null && 
                sessionContent.hasOwnProperty(PUBLIC_KEY) && 
                sessionContent[PUBLIC_KEY] != null &&
                sessionContent.hasOwnProperty(PRODUCTION_KEY) && 
                sessionContent[PRODUCTION_KEY] != null
        }

        /**
         * Validate session token
         * 
         * @param {Object} session 
         * @returns 
         */
        let validateSession = function(session) {
            return typeof session == "object" &&
                session.hasOwnProperty(TOKEN_KEY) &&
                session[TOKEN_KEY] != null &&
                session[TOKEN_KEY] != ""
        }
    }
}