import { Camera } from "../data/camera/api/Camera.js";
import { CryptoJSEncryptor } from "./encryptor/CryptoJSEncryptor.js";
import { Encryptor } from "../../clean/data/encryptor/api/Encryptor.js"
import { FacetecCamera } from "./camera/facetec/FacetecCamera.js";
import { Injector } from "../../di/Injector.js";
import { JWTEncryptor } from "./encryptor/JWTEncryptor.js";
import { UnicoCamera } from "./camera/unico-webframe/UnicoCamera.js"
import { singleton } from "../../di/DependencyInjector.js";
import { System } from "../data/system/api/System.js";
import { PlatformSystem } from "./system/PlatformSystem.js"

const PluginsInjectorFactory = (function() {
    var INSTANCE;

    class PluginsInjector extends Injector {
        constructor() {
            super();

            super.inject = function() {
                
                singleton(Camera, function() {
                    return new UnicoCamera();
                }, "UNICO");

                singleton(Camera, function() {
                    return new FacetecCamera();
                }, "FACETEC");

                singleton(Encryptor, function() {
                    return new CryptoJSEncryptor();
                }, "RIJNDAEL");

                singleton(Encryptor, function() {
                    return new JWTEncryptor();
                }, "JWT");

                singleton(System, function() {
                    return new PlatformSystem();
                })
            }
        }
    }

    return {
        getInstance: function(){
            if (INSTANCE == null) {
                INSTANCE = new PluginsInjector();
                INSTANCE.constructor = null;
            }
            return INSTANCE;
        }
    };
})();

export var PluginsInjector = PluginsInjectorFactory;