import { UnicoHttpRequestType, HttpRequestTypes } from "./UnicoHttpRequestType.js";
import { UnicoHttpException } from "./UnicoHttpException.js";
import { UnicoHttpResponseStatusType } from "./UnicoHttpResponseStatusType.js";
import { UnicoException } from "../../entities/UnicoException.js";

export class UnicoHttpRequest {

    constructor(baseURL, xmlHttpRequest) {

        if(baseURL == null || baseURL == "") {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("baseURL");
        }

        if(typeof baseURL !== "string") {
            throw new UnicoException.GENERAL_INSTANCE_OF("baseURL", "String");
        }

        this.host = function() {
            return baseURL;
        };

        this.changeBaseURL = function(newURL) {
            baseURL = newURL;

            return this;
        }

        this.get = async function(url, headers = {}) {
            return await request(url, UnicoHttpRequestType.GET, headers);
        }
    
        this.post = async function(url, headers = {}, body = {}) {
            return await request(url, UnicoHttpRequestType.POST, headers, body);
        }

        let request = function(url, unicoHttpRequestType, headers, body = null) {

            return new Promise((resolve, reject) => {
    
                if (url == undefined || url == "") {
                    throw new UnicoHttpException(UnicoHttpResponseStatusType.UNKNOWN.code, "URL is required");
                }
    
                if (typeof url !== "string") {
                    throw new UnicoHttpException(UnicoHttpResponseStatusType.UNKNOWN.code, "URL must be an instance of String");
                }
    
                if (unicoHttpRequestType == undefined) {
                    throw new UnicoHttpException(UnicoHttpResponseStatusType.UNKNOWN.code, "unicoHttpRequestType is required");
                }
    
                if (!(unicoHttpRequestType instanceof HttpRequestTypes)) {
                    throw new UnicoHttpException(UnicoHttpResponseStatusType.UNKNOWN.code, "unicoHttpRequestType must be an instance of HttpRequestTypes");
                }
    
                xmlHttpRequest.open(unicoHttpRequestType.name, baseURL + url);
    
                addHeaders(headers);
                addListeners(resolve, reject);
                call(unicoHttpRequestType, body);
    
            });
        };

        let addHeaders = function (headers) {
            xmlHttpRequest.setRequestHeader("Content-Type", "application/json");
            
            for (const [key, value] of Object.entries(headers)) {
                xmlHttpRequest.setRequestHeader(key, value);
            }
        }
        let addListeners = function (resolve, reject) {
            xmlHttpRequest.onreadystatechange = function () {
                try {
                    if (this.readyState === XMLHttpRequest.DONE) {
                        if(this.status == UnicoHttpResponseStatusType.OK.code) {
                            if ( this.getResponseHeader("content-type").includes("application/json") ) {
                                resolve(JSON.parse(this.responseText));
                            } else {
                                resolve(this.responseText);
                            }
                        } else {
                            reject(new UnicoHttpException(this.status, this.statusText, this.responseText));
                        }
                    }
                } catch (e) {
                    reject(new UnicoHttpException(UnicoHttpResponseStatusType.UNKNOWN.code, e.toString()));
                }
            };
            
            xmlHttpRequest.onerror = function () {
                reject(new UnicoHttpException(this.status, this.statusText, this.responseText));
            };
        }
    
        let call = function(unicoHttpRequestType, body) {
            if(unicoHttpRequestType.name == UnicoHttpRequestType.POST.name) {
                xmlHttpRequest.send(JSON.stringify(body));
            } else {
                xmlHttpRequest.send();
            }
        }
    }
}