import { Encryptor } from "../../data/encryptor/api/Encryptor.js";
import { SignJWT } from 'jose-browser-runtime/jwt/sign';

export class JWTEncryptor extends Encryptor {

    constructor() {
        super();

        const EXPIRATION_TIME = "10m";

        super.algorithm = function (salt) {
            return {
                alg: 'HS256',
                typ: 'JWT'
            };
        }

        super.encrypt = function(obj, signature, algorithm) {
            return new SignJWT(obj)
                    .setProtectedHeader(algorithm)
                    .setIssuedAt()
                    .setIssuer(window.location.href)
                    .setExpirationTime(EXPIRATION_TIME)
                    .sign(new TextEncoder().encode(window.atob(signature)));
        }

        super.decrypt = function(encryptedText, signature, algorithm) {

        }
    }
}