import { AuthenticatorData } from "./authenticator/api/AuthenticatorData.js";
import { AuthenticatorRepository } from "./authenticator/repository/AuthenticatorRepository.js";
import { CameraData } from "./camera/api/CameraData.js";
import { CameraRepository } from "./camera/repository/CameraRepository.js";
import { Camera } from "./camera/api/Camera.js";
import { Config } from "../modules/config/Config.js";
import { Encryptor } from "./encryptor/api/Encryptor.js";
import { EncryptorData } from "./encryptor/api/EncryptorData.js";
import { EncryptorRepository } from "./encryptor/repository/EncryptorRepository.js";
import { Injector } from "../../di/Injector.js";
import { LivenessData } from "./liveness/api/LivenessData.js"
import { LivenessRepository } from "./liveness/repository/LivenessRepository.js"
import { UnicoHttpRequest } from "../plugins/networking/UnicoHttpRequest.js";
import { get, singleton } from "../../di/DependencyInjector.js";
import { SystemData } from "./system/api/SystemData.js";
import { SystemRepository } from "./system/repository/SystemRepository.js";
import { System } from "./system/api/System.js";

const DataInjectorFactory = (function() {
    var INSTANCE;

    class DataInjector extends Injector {

        constructor() {
            super();

            super.inject = function() {

                singleton(AuthenticatorData, function() {
                    return new AuthenticatorRepository(new UnicoHttpRequest(window.location.origin, new XMLHttpRequest()));
                }, "LOCAL");

                singleton(AuthenticatorData, function() {
                    return new AuthenticatorRepository(new UnicoHttpRequest(window.location.origin, new XMLHttpRequest()));
                }, "REMOTE");

                singleton(CameraData, function() {
                    return new CameraRepository(get(Camera, "UNICO"));
                });

                singleton(EncryptorData, function() {
                    return new EncryptorRepository(get(Encryptor, "RIJNDAEL"), Config.RIJNDAEL_IV_CODE);
                }, "API");

                singleton(EncryptorData, function() {
                    return new EncryptorRepository(get(Encryptor, "JWT"), Config.JWT_SIGNATURE);
                }, "CLIENT");

                singleton(LivenessData, function() {
                    return new LivenessRepository(new UnicoHttpRequest(window.location.origin, new XMLHttpRequest()));
                });

                singleton(SystemData, function() {
                    return new SystemRepository(get(System));
                });
            }
        }
    }

    return {
        getInstance: function(){
            if (INSTANCE == null) {
                INSTANCE = new DataInjector();
                INSTANCE.constructor = null;
            }
            return INSTANCE;
        }
    };
})();

export var DataInjector = DataInjectorFactory;
