export class DocumentCameraType {
	constructor(name, code, description = null) {
	  this.name = name;
	  this.code = code;
	  this.description = description;
	}

	toString() {
	  return `UnicoCameraType.${this.name}`;
	}
}

export const DocumentCameraTypes = {
	CNH: new DocumentCameraType('CNH', 1),
	CPF: new DocumentCameraType('CPF', 3),
	OTHERS: function(description) { return new DocumentCameraType('OTHERS', 5, description) },
	RG_FRENTE: new DocumentCameraType('RG_FRENTE', 6),
	RG_VERSO: new DocumentCameraType('RG_VERSO', 7),
	RG_FRENTE_NOVO: new DocumentCameraType('RG_FRENTE_NOVO', 8),
	RG_VERSO_NOVO: new DocumentCameraType('RG_VERSO_NOVO', 9),
  CNH_FRENTE: new DocumentCameraType('CNH_FRENTE', 10),
  CNH_VERSO: new DocumentCameraType('CNH_VERSO', 11),
}

export class SelfieCameraType {
	constructor(name, code) {
	  this.name = name;
	  this.code = code
	}

	toString() {
	  return `UnicoCameraType.${this.name}`;
	}
}

export const SelfieCameraTypes = {
	NORMAL: new SelfieCameraType('NORMAL', 1),
	SMART: new SelfieCameraType('SMART', 2),
}
