import { MainCameraPresentation } from "../../presentation/api/MainCameraPresentation.js";

export class CameraOpener {

    /**
     * Unico camera opener
     *
     * @param {Config} config
     * @param {Theme} theme
     * @param {MainCameraPresentation} presenter
     * @param {Camera} camera
     */
    constructor(config, theme, presenter, camera) {
        this.config = config;
        this.theme = theme;
        this.presenter = presenter;
        this.camera = camera;
    }

    /**
     * Initialize camera
     *
     * @abstract
     */
    initialize() {
        // Do nothing
    }

    /**
     * Open camera
     *
     * @abstract
     * @param {Listener} listener
     * @param {UnicoCameraType} unicoCameraType
     * @returns Promise
     */
    async open(listener, unicoCameraType) {
        return Promise();
    }
}
