export const UnicoSDKTheme = (function(){
    return {
        getTheme: function(unicoTheme){

            let customization = {
              silhouette: {
                primaryColor: unicoTheme.colorSilhouetteSuccess,
                secondaryColor: unicoTheme.colorSilhouetteError,
                neutralColor: unicoTheme.colorSilhouetteNeutral,
              },
              buttonCapture: {
                backgroundColor: unicoTheme.backgroundColorButtons,
                iconColor: unicoTheme.colorTextButtons,
              },
              popupLoadingHtml: unicoTheme.htmlPopupLoading,
              boxMessage: {
                backgroundColor: unicoTheme.backgroundColorBoxMessage,
                fontColor: unicoTheme.colorTextBoxMessage
              },
              boxDocument: {
                backgroundColor: unicoTheme.backgroundColorComponents,
                fontColor: unicoTheme.colorTextComponents
              }
            };
            
            return customization;
        }
    };
})(); 