export class UnicoTheme {
    
    constructor(
        colorSilhouetteNeutral, 
        colorSilhouetteSuccess,
        colorSilhouetteError,
        backgroundColor,
        colorText,
        backgroundColorComponents,
        colorTextComponents,
        backgroundColorBoxMessage,
        colorTextBoxMessage,
        backgroundColorButtons,
        colorTextButtons,
        htmlPopupLoading
    ) {

        //silhouette
        this.colorSilhouetteSuccess = colorSilhouetteSuccess;
        this.colorSilhouetteError = colorSilhouetteError;
        this.colorSilhouetteNeutral = colorSilhouetteNeutral;
        //generic's background and texts
        this.backgroundColor = backgroundColor;
        this.colorText = colorText;
        //component's background and texts
        this.backgroundColorComponents = backgroundColorComponents;
        this.colorTextComponents = colorTextComponents;
        //buttons
        this.backgroundColorButtons = backgroundColorButtons;
        this.colorTextButtons = colorTextButtons;
        //message's colors
        this.backgroundColorBoxMessage = backgroundColorBoxMessage;
        this.colorTextBoxMessage = colorTextBoxMessage;
        //popup
        this.htmlPopupLoading = htmlPopupLoading;
    }
}