import { CameraData } from "../api/CameraData.js"
import { Camera } from "../api/Camera.js"
import { UnicoException } from "../../../entities/UnicoException.js";

export class CameraRepository extends CameraData {

    /**
     * Camera Repository
     * 
     * @param {Camera} camera 
     */
    constructor(camera) {
        super();

        if(camera == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("camera");
        }

        if(!(camera instanceof Camera)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("camera", "Camera");
        }

        super.loadCameraModels = function(filePath) {
            return camera.loadModels(filePath);
        }
    }
}