import * as acessoWebFrameSupport from "./frame-support.js";
import * as acessoWebFrameTypes from "./frame-types.js";
import * as maskPNG from "./Mask/strings-base64-documents-png.js";

function createMaskPNG(sillhoutte, videoWidth, videoHeight) {
    setSillhoutteSrc(sillhoutte)
    setSillhoutteSize(sillhoutte, videoWidth, videoHeight)
}

function setSillhoutteSrc(sillhoutte) {

    switch (acessoWebFrameTypes.TYPE_PROCESS_DOCUMENT) {
        case acessoWebFrameTypes.TYPE_DOCUMENT.CNH:
            sillhoutte.src = maskPNG.strBase64CNH;
            break;
        case acessoWebFrameTypes.TYPE_DOCUMENT.CNH_FRONT:
            sillhoutte.src = maskPNG.strBase64CNHFront;
            break;
        case acessoWebFrameTypes.TYPE_DOCUMENT.CNH_BACK:
            sillhoutte.src = maskPNG.strBase64CNHBack;
            break;
        case acessoWebFrameTypes.TYPE_DOCUMENT.RG_BACK:
            sillhoutte.src = maskPNG.strBase64RGBack;
            break;
        case acessoWebFrameTypes.TYPE_DOCUMENT.RG_FRONT:
            sillhoutte.src = maskPNG.strBase64RGFront;
            break;
        case acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_BACK:
            sillhoutte.src = maskPNG.strBase64NewRGBack;
            break;
        case acessoWebFrameTypes.TYPE_DOCUMENT.NEW_RG_FRONT:
            sillhoutte.src = maskPNG.strBase64NewRGFront;
            break;
        default:
            sillhoutte.src = maskPNG.strBase64CPF;
    }
}

function setSillhoutteSize(sillhoutte, videoWidth, videoHeight) {
    let percentMargin = 20;
    let sillhoutteHeight = (videoHeight - getPercentageMarginSillhoutte(percentMargin, videoHeight))
    let sillhoutteWidth = (videoWidth - getPercentageMarginSillhoutte(percentMargin, videoWidth))
    sillhoutte.style.height = sillhoutteHeight + "px"
    sillhoutte.style.width = sillhoutteWidth + "px"
}

function getPercentageMarginSillhoutte(percentToGet, fromValue) {
    return (percentToGet / 100) * fromValue;
}

export {
    createMaskPNG
}