export class HttpResponseStatusType {
	
	constructor(code, message) {
	  this.message = message;
	  this.code = code;
	}

	toString() {
	  return `HttpResponseStatusType.${this.code}-${this.message}`;
	}
}

export const UnicoHttpResponseStatusType = {
	UNKNOWN: new HttpResponseStatusType(0, "Unknown"),
	OK: new HttpResponseStatusType(200, "OK")
}