import { SystemData } from "../../../data/system/api/SystemData.js";
import { UnicoException } from "../../../entities/UnicoException.js";
import { GetSystemInfoUseCase } from "../useCases/GetSystemInfoUseCase.js";

export class GetSystemInfoInteractor extends GetSystemInfoUseCase {

    constructor(systemRepository) {
        super();

        if(systemRepository == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("systemRepository");
        }

        if(!(systemRepository instanceof SystemData)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("systemRepository", "SystemData");
        }

        super.execute = function() {
            return systemRepository.getInfo();
        }
    }
}