import { Camera } from "../../../data/camera/api/Camera.js";
import { CameraOpener } from "../main/ui/api/CameraOpener.js";
import { SelfieCameraPresentation } from "../selfie/presentation/api/SelfieCameraPresentation.js";
import { UnicoException } from "../../../entities/UnicoException.js";
import { get } from "../../../../di/DependencyInjector.js";

const DocumentCameraFactory = (function () {

  class DocumentCameraView extends CameraOpener {

    /**
     * Unico check selfie camera
     *
     * @param {Config} config
     * @param {Theme} theme
     * @param {SelfieCameraPresentation} presenter
     * @param {Camera} camera
     * @param {String} hostURL
     * @param {String} hostAPIKey
     */
    constructor(config, theme, presenter, camera, hostURL, hostAPIKey) {
      super(config, theme, presenter);

      const KEY_IMAGE = "image";

      super.initialize = function () {
        return presenter.initialize(config.models)
      }

      super.open = async function (listener) {
        var userAgent;
        try {
          userAgent = camera.getUserAgent()
        } catch (e) {
          userAgent = ""
        };

        try {
          const responseCamera = await camera.initCamera(config, theme);

          if (validateString(responseCamera, KEY_IMAGE)) {

            const encryptedImage = await presenter.encrypt({
              image: responseCamera.image,
              info: responseCamera.info,
              auth: config.token
            }, window.btoa(config.token));

            listener.on.success({
              base64: responseCamera.image,
              encrypted: encryptedImage
            });

          } else {
            throw UnicoException.CAMERA_PARSE_ERROR;
          }
        } catch (error) {
          listener.on.error(error);
        }
      }

      let validateString = function (obj, key) {
        return typeof obj === "object" &&
          obj.hasOwnProperty(key) &&
          typeof obj[key] === "string" &&
          obj[key] != null &&
          obj[key] != ""
      }
    }
  }

  return {
    getInstance: function (config, theme, hostURL, hostAPIKey) {

      return new DocumentCameraView(
        config,
        theme,
        get(SelfieCameraPresentation, "PASSIVE"),
        get(Camera, "UNICO"),
        hostURL,
        hostAPIKey
      );
    }
  };
})();

export var DocumentCameraView = DocumentCameraFactory
