import { AuthenticateUseCase } from "../../../../../domain/authenticator/useCases/AuthenticateUseCase.js";
import { EncryptUseCase } from "../../../../../domain/encryptor/useCases/EncryptUseCase.js";
import { LoadModelUseCase } from "../../../../../domain/camera/useCases/LoadModelUseCase.js";
import { SelfieCameraPresentation } from "../api/SelfieCameraPresentation.js";
import { UnicoException } from "../../../../../entities/UnicoException.js";

export class PassiveSelfieCameraPresenter extends SelfieCameraPresentation {

    /**
     * Implementation of Selfie camera
     */
    constructor(authenticateUseCase, loadModelUseCase, encryptUseCase) {
        super();

        const KEY_TOKEN = "Token";
        const KEY_IMAGE = "image";
        const KEY_INFO = "info"

        if(authenticateUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("authenticateUseCase");
        }

        if(!(authenticateUseCase instanceof AuthenticateUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("authenticateUseCase", "AuthenticateUseCase");
        }

        if(loadModelUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("loadModelUseCase");
        }

        if(!(loadModelUseCase instanceof LoadModelUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("loadModelUseCase", "LoadModelUseCase");
        }

        if(encryptUseCase == null) {
            throw new UnicoException.GENERAL_MISSING_PROPERTY("encryptUseCase");
        }

        if(!(encryptUseCase instanceof EncryptUseCase)) {
            throw new UnicoException.GENERAL_INSTANCE_OF("encryptUseCase", "EncryptUseCase");
        }
        
        super.initialize = function(filePath) {
            return loadModelUseCase
                .execute(filePath)
                .catch(UnicoException.CAMERA_MODELS_NOT_LOADED);
        }

        super.getCameraSession = async function(host, userAgent, apiKey, token) {
            var session;
            
            try {
                session = await authenticateUseCase.execute(host, apiKey, token)
            } catch(e) {
                throw UnicoException.SESSION_REQUEST_ERROR;
            }

            if(!validateSession(session)) {
                throw UnicoException.SESSION_TOKEN_NOT_FOUND;
            }

            return session;
        }
        
        super.encrypt = function(result, signature) {
            try {
                return encryptUseCase.execute(result, signature)
            } catch(e) {
                throw UnicoException.ENCRYPTION_ERROR;
            }
        }

        super.validate = async function(result) {
            if(validateString(result, KEY_IMAGE) && validateObject(result, KEY_INFO)) {
                return result;
            } else {
                throw UnicoException.CAMERA_PARSE_ERROR;
            }
        }

        let validateSession = function(session) {
            return typeof session == "object" &&
                session.hasOwnProperty(KEY_TOKEN) &&
                typeof session[KEY_TOKEN] == "string" &&
                session[KEY_TOKEN] != null &&
                session[KEY_TOKEN] != ""
        }

        let validateString = function(obj, key) {
            return obj !== undefined &&
                obj != null &&
                typeof obj === "object" &&
                obj.hasOwnProperty(key) && 
                typeof obj[key] === "string" &&
                obj[key] != null &&
                obj[key] != ""
        }

        let validateObject = function(obj, key) {
            return obj !== undefined &&
                obj != null &&
                typeof obj === "object" &&
                obj.hasOwnProperty(key) && 
                typeof obj[key] === "object" &&
                obj[key] != null &&
                Object.values(obj[key]).length > 0
        }
    }
}