import { UnicoException } from "../clean/entities/UnicoException.js";

const DependencyInjectorFactory = (function() {
    var INSTANCE;

    class DependencyInjector {

        constructor() {

            /**
             * Instance storage
             *
             * @readonly
             */
            var classMap = {};

            /**
             * Register singleton
             *
             * @param {String} jsClass
             * @param {Function} launcher
             * @param {String} named
             */
            this.single = function(jsClass, launcher, named = "default") {

                if(typeof jsClass === "undefined") {
                    throw new UnicoException.GENERAL_MISSING_PROPERTY("jsClass");
                }

                if(typeof launcher !== "function") {
                    throw new UnicoException.GENERAL_INSTANCE_OF("launcher", "Function");
                }

                let name = named != "" ? named : "default";

                const classRegex = new RegExp(/(?:class)(.+?)(?:\s?)(?:\{)/g);
                let classMatch = classRegex.exec(jsClass.toString());

                const functionRegex = new RegExp(/(?:function)(.+?)(?:\s?)(?:\()/g);
                let functionMatch = functionRegex.exec(jsClass.toString());

                if (classMatch != null && classMatch.length > 0) {
                    var className = classMatch[1].trim();
                } else if (functionMatch != null && functionMatch.length > 0) {
                    var className = functionMatch[1].trim();
                } else {
                    throw new UnicoException.DI_INVALID_INSTANCE();
                }

                let key = `${className}_${name}`;

                if(!(launcher() instanceof jsClass)) {
                    throw new UnicoException.GENERAL_INSTANCE_OF("Injected class", className);
                }

                classMap[key] = {
                    isInitialized: false,
                    isSingleton: true,
                    launcher: launcher
                };
            }

            /**
             * Get class
             *
             * @param {String} jsClass
             * @param {String} named
             * @returns Any
             */
            this.get = function(jsClass, named = "default") {
                let name = named != "" ? named : "default";

                const classRegex = new RegExp(/(?:class)(.+?)(?:\s?)(?:\{)/g);
                let classMatch = classRegex.exec(jsClass.toString());

                const functionRegex = new RegExp(/(?:function)(.+?)(?:\s?)(?:\()/g);
                let functionMatch = functionRegex.exec(jsClass.toString());

                if (classMatch != null && classMatch.length > 0) {
                    var className = classMatch[1].trim();
                } else if (functionMatch != null && functionMatch.length > 0) {
                    var className = functionMatch[1].trim();
                } else {
                    throw new UnicoException.DI_INVALID_CLASS();
                }

                let key = `${className}_${name}`;
                let dependency = classMap[key];
                if(dependency == undefined) {

                    throw new UnicoException.DI_MISSING_CLASS(className, name);
                }

                if(dependency.isSingleton && !dependency.isInitialized) {
                    dependency.instance = classMap[key].launcher();
                    dependency.isInitialized = dependency.instance !== undefined;
                }

                classMap[key] = dependency;

                return dependency.instance;
            }

            /**
             * Clear injected instances
             */
            this.clear = function() {
                classMap = {}
            }
        }
    }

    return {
        getInstance: function(){
            if (INSTANCE == null) {
                INSTANCE = new DependencyInjector();
                INSTANCE.constructor = null;
            }
            return INSTANCE;
        }
    };
})();

export const DependencyInjector = DependencyInjectorFactory;

/**
 * Load dependency injectors
 * @param  {...Injector} injectors
 */
export let loadInjectors = function(...injectors) {
    injectors.forEach(value => {
        value.inject();
    });
}

/**
 * Store dependency
 *
 * @param {Class} jsClass
 * @param {Function} launcher
 */
export let singleton = function(jsClass, launcher, named) {
    DependencyInjector.getInstance().single(jsClass, launcher, named);
}

/**
 * Get stored dependency
 *
 * @param {Class} jsClass
 * @returns Any
 */
export let get = function(jsClass, named) {
    return DependencyInjector.getInstance().get(jsClass, named);
}
