import CryptoES from "crypto-es";
import { Encryptor } from "../../data/encryptor/api/Encryptor.js";
import { UnicoException } from "../../entities/UnicoException.js";

export class CryptoJSEncryptor extends Encryptor {

    constructor() {
        super();

        super.algorithm = function (salt) {
            return {
                mode: CryptoES.mode.CBC,
                padding: CryptoES.pad.Pkcs7,
                iv: CryptoES.enc.Hex.parse(salt)
            };
        }

        super.encrypt = function(obj, signature, algorithm) {
            return new Promise((resolve) => {
                
                if(typeof obj === "string" ) {
                    obj = CryptoES.enc.Utf8.parse(obj)
                }
                
                let keyCode = CryptoES.enc.Hex.parse(signature);

                resolve(CryptoES.AES.encrypt(obj, keyCode, algorithm).toString());
            });
        }

        super.decrypt = function(encryptedText, signature, algorithm) {
            return new Promise((resolve) => {
                
                if(typeof encryptedText !== "string" ) {
                    reject(UnicoException.DI_INVALID_INSTANCE("encryptedText", "String"));
                }
                
                let keyCode = CryptoES.enc.Hex.parse(signature);

                resolve(CryptoES.AES.decrypt(encryptedText, keyCode, algorithm).toString(CryptoES.enc.Utf8));
            })
        }
    }
}