import { UnicoTheme } from "./UnicoTheme.js";


export const UnicoThemeDefault = (function(){
    return {
        get: function() {
            return new UnicoTheme(
                '#C4C4C4',
                '#22C532',
                '#FFCB00',
                '#F9F8FA',
                '#404040',
                '#1172EB',
                '#FFFFFF',
                '#1172EB',
                '#FFFFFF',
                '#1172EB',
                '#FFFFFF',
                '<div style="position: absolute; top: 45%; right: 50%; transform: translate(50%, -50%); z-index: 10; text-align: center;">Carregando...</div>'
            );
        },
        merge: function(theme, themeDefault){ 

            if(!theme.colorSilhouetteSuccess) {
                theme.colorSilhouetteSuccess = themeDefault.colorSilhouetteSuccess;
            }
            if(!theme.colorSilhouetteNeutral) {
                theme.colorSilhouetteNeutral = themeDefault.colorSilhouetteNeutral;
            }
            if(!theme.colorSilhouetteError) {
                theme.colorSilhouetteError = themeDefault.colorSilhouetteError;
            }
            if(!theme.backgroundColor) {
                theme.backgroundColor = themeDefault.backgroundColor;
            }
            if(!theme.colorText) {
                theme.colorText = themeDefault.colorText;
            }
            if(!theme.backgroundColorComponents) {
                theme.backgroundColorComponents = themeDefault.backgroundColorComponents;
            }
            if(!theme.colorTextComponents) {
                theme.colorTextComponents = themeDefault.colorTextComponents;
            }
            if(!theme.backgroundColorButtons) {
                theme.backgroundColorButtons = themeDefault.backgroundColorButtons;
            }
            if(!theme.colorTextButtons) {
                theme.colorTextButtons = themeDefault.colorTextButtons;
            }
            if(!theme.backgroundColorBoxMessage) {
                theme.backgroundColorBoxMessage = themeDefault.backgroundColorBoxMessage;
            }
            if(!theme.colorTextBoxMessage) {
                theme.colorTextBoxMessage = themeDefault.colorTextBoxMessage;
            }
            if(!theme.htmlPopupLoading) {
                theme.htmlPopupLoading = themeDefault.htmlPopupLoading;
            }

            return theme;
        }
    };
})();
